@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);

*{
  margin:0;
  padding:0;
  box-sizing: border-box;
}

body{
  font-family: 'Roboto', sans-serif;
  background: skyblue;
}

#quote-box{
  background: #ffff;
  width: 40rem;
  margin: 10rem auto;
  padding: 3rem;
  border-radius: 5px;
}

#text{
  font-size: 2rem;
  margin-bottom: 1.5rem;
}

#author{
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: flex-end;
          justify-content: flex-end;
  font-size: 1.2rem;
}

#buttons{
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  margin-top: 2rem;

}

.social-media > a{
  margin-right: .5rem;
  padding: .3rem .5rem;
  cursor: pointer;
  border-radius: 3px;

}

.social-media > a:hover{
  opacity: 0.9;
}


.social-media > a img{
  width: 1.5rem;

}

#new-quote{
  background: #0059ff;
  border-radius:2px;
  color: #ffff;
  padding: .7rem 1rem;
  font-size: .9rem;
  font-weight: bold;
  border: none ;
  outline: none;
  cursor: pointer;
}

#new-quote:hover{
  opacity: 0.9;
}
